* {
  font-family:  Helvetica, Arial, sans-serif
}
body{
  padding: 60px;
}
input {
  font-size: 1.2rem;
  color: grey;
  width: 100%;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-right: 10px;
  margin-bottom: 10px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}